<template>
  <div class="home">
    <h1>模拟面试</h1>
    <div class="main">
      <div class="choiceQuestion">
        <el-select @change="changeQuestionList" v-model="choiceQuestion" placeholder="请选择问题范围">
          <el-option v-for="(item, index) in questionData" :key="index" :label="item.name" :value="item.name"></el-option>
        </el-select>
        <el-button class="reset" @click="reset" type="warning" round>重置</el-button>
      </div>

      <div class="questionTitle">
        <h2>请简单介绍一下</h2>
        <p>{{showQuestion.Q}}</p>
        <el-button class="nextQuestion" @click="randomQuestion" type="primary" round>下一题</el-button>
      </div>

      <div class="questionAnswer">
        <el-button @click="showAnswerFlag = true" type="primary" round>查看答案</el-button>
        <p v-if="showAnswerFlag">{{showQuestion.A}}</p>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'Home',
  data(){
    return{
      choiceQuestion: '',
      questionData: [],
      questionList: [],
      showQuestion: {},
      showAnswerFlag: false,
      backupsData: [
        {
          name: 'Html、Css',
          question: [
            {
              Q: 'H5的新特性',
              A: '语义化标签有:header,footer,aside,nav等。还有在音频audio和视频video方面的改进。本地存储localStorage和SessionStorage以及拖拽draggable'
            },
            {
              Q: 'C3的新特性',
              A: '新增圆角(border-radius)、过渡(transition)、位移(translate)、边框阴影(box-shadow)等'
            }
          ]
        },
        {
          name: 'javaScript',
          question: [
            {
              Q: 'js基本数据类型有什么',
              A: 'null、undefined、string、number、boolean'
            },
            {
              Q: 'js的引用类型有什么',
              A: '数组、对象、函数、日期、Math'
            },
          ]
        },
      ]
    }
  },
  methods:{
    changeQuestionList(){
      this.questionList = this.questionData.filter(item => item.name == this.choiceQuestion)[0].question
      this.randomQuestion()
    },
    randomQuestion(){
      this.showAnswerFlag = false
      let length = this.questionList.length
      this.showQuestion = {}
      if(length == 0){
        this.$message({
          type: 'warning',
          message: '此类问题回答完毕，请选择其他类问题！'
        })
        return
      }
      let index = Math.floor(Math.random() * length)
      this.showQuestion = this.questionList[index]
      this.questionList.splice(index, 1)
      console.log(this.backupsData)
    },
    reset(){
      this.questionData = JSON.parse(JSON.stringify(this.backupsData))
      this.choiceQuestion = ''
    }
  },
  mounted(){
    this.reset()
  },
  components: { 

  }
}
</script>
<style scoped lang="scss">
.main{
  position: relative;
  top: 15vh;
  left: 25vw;
  width: 50vw;
  height: 60vh;
  background: #e1e1e1;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  .choiceQuestion{
    position: relative;
    padding-left: 1vw;
    height: 6vh;
    padding-top: calc(3vh - 20px);
    text-align: left;
    .reset{
      position: absolute;
      right: 1vw;
    }
  }
  .questionTitle{
    flex: 1;
    position: relative;
    border-bottom: 1px solid #ccc;
    .nextQuestion{
      position: absolute;
      bottom: 1.5vh;
      left: 50%;
      transform: translate(-50%, 0);
    }
  }
  .questionAnswer{
    flex: 1;
    border-top: 1px solid #ccc;
    padding-top: 1.5vh;
  }
}
</style>